
.reps {
  margin-right: 30px;
  margin-top: -20px;
  padding-top: 20px;
  padding-bottom: 15px;
  color: orange;
  font-size: 16px;
}
.statement {
  font-size: 12px;
  color: orange;
}
hr {
  margin-left: 1rem;
  margin-right: 1rem;
  margin-top: 25px;
  border-width: 3px;
  padding-bottom: 5px;
  background-color: #f5a608;
}
.myFooter {
  background: #0f0e19;
  border-top: 10px solid black;
  padding: .2rem 2rem 3rem 2rem;
 
  font-size: .75rem;
}
.list-inline {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
li {
  margin-left: 60px;
}
li a {
  font-size: 1.5rem;
}
.myLinks a {
  color: orange;
}
.myLinks a:hover,
.myLinks a:focus {
  color: #ffcc6f;
  text-decoration: none;
}
.tdo a {
 
  margin-top: 20px;
  padding-bottom: 10px;
  font-size: 14px;
}
.admin a {
  color: #8a1e31;
}
.admin a:hover,
.admin a:focus {
  color: white;
  text-decoration: none;
}
@media screen and (max-width: 500px) {
.list-inline li {
    display: block;
}
}




